import React, {useState, useEffect} from 'react'
import './Inactive.css'
import { Link } from 'react-router-dom';
import axios from "../axios"
import setAuthToken from './Utils/setAuthToken'
import './Active.css';
import {
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
} from "@chakra-ui/react"

function Inactive() {
    const [ data, setData] = useState([]);
    const [ques, setQues] = useState([]);

    useEffect(() => {
      async function fetchData(){
        const token = localStorage.getItem("my_token");
        const id = localStorage.getItem("user")
        setAuthToken(token);
        const req = await axios.get('/pay');
        const q = await axios.post('/data', {user: id});
        setQues(q.data);
        setData(req.data);
    }
  
    fetchData();
    }, [])
  return (
    <div>
    <div className="overview">
        <Table>
          <Thead>
            <Tr>
              <Th>Name</Th>
              <Th>Email</Th>
              <Th>Status</Th>
              <Th>Amount</Th>
              <Th>Phone</Th>
              <Th>{ques.q1}</Th>
              <Th>{ques.q2}</Th>
              <Th>{ques.q3}</Th>
            </Tr>
          </Thead>
          <Tbody>
          
          {data.map(data1=>(
            (data1.status=="Pending")?(
              <Tr>
              <Td>{data1.buyer_name}</Td>
              <Td>{data1.email}</Td>
              <Td>{data1.status}</Td>
              <Td>{data1.amount}</Td>
              <Td>{data1.phone}</Td>
              <Td>{data1.a1}</Td>
              <Td>{data1.a2}</Td>
              <Td>{data1.a3}</Td>
              </Tr>
            
            ): ""
          ))}
          
          </Tbody>
        </Table>
    </div>
    </div>
)
}

export default Inactive
