import React from 'react'
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import ActiveMember from './Pages/activeMember';
import Form from './Components/Form/formCode';
import Login from './Components/Login';
import MemberOverview from './Pages/memberOverview';
import Thanks from './Components/thanks';
import Test from './Components/test';
import InactiveMember from './Pages/inactiveMember';

function App() {
  return (
    <>
      <Router>  
        <Switch>
          <Route path="/" exact component = {Thanks}/>
          <Route path="/login" exact component = {Login}/>
          <Route path="/form/:id/:amount" exact component = {Form}/>
          <Route path="/thanks" exact component = {Thanks}/>
          <Route path="/activemember" exact component = {ActiveMember}/>
          <Route path="/memberoverview" exact component = {MemberOverview}/>
          <Route path="/inactivemember" exact component = {InactiveMember}/>
          <Route path="/test" exact component = {Test}/>

        </Switch>
      </Router>
    </>
  )
}

export default App
