import React, {useState} from 'react'
import { Link } from 'react-router-dom'
import './navbar.css'
import Button from '@material-ui/core/Button';
import { AiOutlineMenu } from "react-icons/ai";
import {
    Drawer,
    DrawerBody,
    DrawerFooter,
    DrawerHeader,
    DrawerOverlay,
    DrawerContent,
    useDisclosure,
    Input,
    RadioGroup,
    Stack,
    Radio,
    DrawerCloseButton,
    IconButton,
  } from "@chakra-ui/react"
// import Sidebar from './Sidebar';

function Navbar() {
    const { isOpen, onOpen, onClose } = useDisclosure()

    const btnRef = React.useRef()

    const token = localStorage.getItem("my_token");
    const user = localStorage.getItem("user");
    const email = localStorage.getItem("email");

    function Logout(){
      console.log("clicked")
      localStorage.clear();
      window.location.reload()
    }    
    
    return (
        <div className="dash">
            <Drawer
              isOpen={isOpen}
              placement="left"
              onClose={onClose}
              finalFocusRef={btnRef}
            >
              <DrawerOverlay />
              <DrawerContent>
                <DrawerCloseButton />
                <DrawerHeader>
                <div className="nav-logo"><img src="techbud_final2.png" alt="logo"/></div>
                <div className="menu">
                <div className="user">
                  {token && user? (
                    <div className="logout">
                      <br></br>
                      <h3>Hi, {email}</h3>
                      <br></br>
                      <Button className="logout" onClick={Logout}>Log Out</Button>
                      <br></br>
                    </div>
                  ) : (
                    <Link to="/login">
                    <Button>Login</Button>
                    </Link>
                )}
                </div></div>
                </DrawerHeader>
      
                <DrawerBody>
                
            <div>
                <div className="menu-item"><Button width="400px"><Link to="/memberoverview">Member Overview</Link></Button></div>
                <div className="menu-item"><Button><Link to="/activemember">Active Member</Link></Button></div>
                <div className="menu-item"><Button><Link to="/inactivemember">Inactive Member</Link></Button></div>
            </div>
                </DrawerBody>
      
                <DrawerFooter>
                  Copyright @techbud.in
                </DrawerFooter>
              </DrawerContent>
            </Drawer>
        <div className="navbar">
            <div className="button">
            <IconButton
                variant="outline"
                colorScheme="teal"
                aria-label="Call Sage"
                fontSize="20px"
                icon={<AiOutlineMenu />}
                ref={btnRef} colorScheme="teal" onClick={onOpen}
            />                
            </div>
            <div className="logo">
                <img src="techbud_final2.png" alt="logo"/>
            </div>
        </div>
        </div>
    )
}

export default Navbar

