import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom';
import axios from "../axios"
import setAuthToken from './Utils/setAuthToken'
import "./Overview.css";
import Button from '@material-ui/core/Button';
// import CameraIcon from '@material-ui/icons/PhotoCamera';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import {Line} from "react-chartjs-2"


function Overview() {
  
    const [ data, setData] = useState([]);
    const [ labelData, setLabelData] = useState([]);
    // const [ chartData, setchartData] = useState({
    //   label: ['boston', 'toston', 'rere'],
    //   dataSet: [{
    //     data: [
    //       443,323,234
    //     ],  
    //   }]
    // })

    useEffect(() => {
      async function fetchData(){
        const token = localStorage.getItem("my_token");
        setAuthToken(token);
        const req = await axios.get('/pay');
  
        setData(req.data);
    }
  
    fetchData();
    }, [])
  
    function Total(data){
      var total = 0;
     
        data.map(data1=>(
          (data1.status=="Success")?(total = total + data1.amount)
          :""
        ))
        console.log(total);
      return total;
    }

    function Label(data){
      data.map(data1=>(
        (data1.status=="Success")?(setLabelData(data1.startdate)):""))
    }
    
    function TotalMember(data){
      var total = 0;
      data.map(data1=>(
        (data1.status=="Success")?(total = total + 1)
        :""        
      ))
      console.log(total);
      return total;
    }
  
    return (
      <div class="overview">
        
        
        <div className="Cards">
          <Grid>
            <Card className="card1">
              <CardContent >
                <Typography gutterBottom variant="h5" component="h2" className="centre">
                  <center>
                    Total Member
                  </center>
                </Typography>
                <Typography>
                  <h2>
                    <center>
                      {TotalMember(data)}
                    </center>
                  </h2>
                </Typography>
              </CardContent>
                <CardActions>
                  <Button size="small" color="primary" className="cardview">
                    View
                  </Button>
                </CardActions>
              </Card>
          </Grid>
          <Grid>
            <Card className="card2">
              <CardContent >
                <Typography gutterBottom variant="h5" component="h2">
                  <center>
                    Total Amount
                  </center>
                </Typography>
                <Typography>
                  <h2>
                    <center>
                      Rs {Total(data)}
                    </center>
                  </h2>
                </Typography>
              </CardContent>
              <CardActions>
                <Button size="small" color="primary" className="cardview">
                  View
                </Button>                   
              </CardActions>
            </Card>
          </Grid>
              
        </div> 
        <div className="relative">               
        <Line
          data = {
            {
              labels: ["eef", "ede", "efef"],
              datasets: [{
                label: 'Amount Received',
                data: [12, 19, 3],
              }]
            }}
            height =  {100}
            width = {400}         
        /> 
        </div>            
      </div>
    )
    
}

export default Overview
