import React, {useState, useEffect} from 'react'
import axios from '../../axios'
import {
    FormControl,
    FormLabel,
    Input,
    FormHelperText,
    Button,
    Radio, RadioGroup, HStack,Checkbox, Select, Center
  } from "@chakra-ui/react"
import FormNavbar from './formNavbar';
import './formCode.css'
import FormFooter from './formFooter';
import {useParams} from 'react-router'


const initialState = {email: '', password: ''};

function Form() {
    let {id,amount} = useParams(    );
    console.log("id and amount",id,amount);
    const [formData, setFormData] =useState(initialState);
    const [formq, setFormq] = useState([]);

    useEffect(() => {

        async function FetchData(){
        console.log("inside")
        const req = await axios.post('/data', {user: id});
        console.log("hehehehe",id,req.data)
        setFormq(req.data);
    }
  
    FetchData();
    }, [])

    function buyNow(formData){
        // console.log("clicked");
        const data = {
          ...formData,
          purpose: 'test',
          amount: `${amount}`,
          from: 'MSME Business Forum',  
          redirect_url: `http://localhost:8001/callback?user_id=${id}`,
        };
        console.log(data);
        axios.post(`/pay/${id}`, data)
          .then(res=>{
            console.log('resp', res.data);
            window.location.href = res.data;
          })
          .catch((error)=>console.log(error.response.data));
      }

    const handleSubmit = (e) => {
        e.preventDefault();
        console.log(formData);
       // axios.post('./pay/6085bd330fd10a1fa00fc86f', formData)
       buyNow(formData);

    };

    const handleChange = (e) => {
        console.log('hi');
        setFormData({...formData, [e.target.name]: e.target.value});
    };

    return (
        <div>
            <FormNavbar/>
            <div className="form">
            <form onSubmit={handleSubmit}>
                <div className="forml">
                <FormControl id="email" isRequired >
                    <FormLabel>Email address</FormLabel>
                    <Input type="email" name="buyer" className="input" onChange={handleChange}/>
                    <FormHelperText>We'll never share your email.</FormHelperText>
                </FormControl>
                <FormControl id="name" isRequired >
                    <FormLabel>Full Name</FormLabel>
                    <Input type="text" className="input" name="buyer_name" onChange={handleChange}/>
                    <FormHelperText>Your name helps us identify you</FormHelperText>
                </FormControl>
                <FormControl id="phone" isRequired >
                    <FormLabel>Phone Number</FormLabel>
                    <Input type="text" name="buyer_phone" className="input" onChange={handleChange} maxlength="10"/>
                    <FormHelperText>Your Phone number is always kept private.</FormHelperText>
                </FormControl>   
                </div>
                <div className="formr">
                <FormControl id="cname">
                    <FormLabel>{formq.q1}</FormLabel>
                    <Input type="text" name="a1" className="input" onChange={handleChange}/>
                    <FormHelperText>{formq.d1}</FormHelperText>
                </FormControl>   
                <FormControl id="typeofBiz" isRequired >
                    <FormLabel>{formq.q2}</FormLabel>
                    <Input type="text" name="a2" className="input" onChange={handleChange}/>
                    <FormHelperText>{formq.d2}</FormHelperText>
                </FormControl>      
                <FormControl>
                <FormLabel>{formq.q3}</FormLabel>
                    <Input type="text" name="a3" className="input" onChange={handleChange}/>
                    <FormHelperText>{formq.d3}</FormHelperText>
                </FormControl>        
                </div>
                <div className="terms">
                <FormControl>
                    <RadioGroup>
                    <HStack spacing="24px">
                        <Checkbox value="0-5" isRequired>I agree to techbud's privacy policy</Checkbox>                      
                    </HStack>
                    </RadioGroup>
                </FormControl> 
                <FormControl>
                    <RadioGroup>
                    <HStack spacing="24px">
                        <Checkbox value="0-5" isRequired>I agree to MSME Business Forum's Terms of Use for Users and Privacy Policy.*
                    I also agree to receive communications regarding events, news and promotions. I can unsubscribe at any time.</Checkbox>                    
                    </HStack>
                    </RadioGroup>
                </FormControl> 
                </div>
                <Center>
                <Button className="submit" colorScheme="blue" type="submit"> Submit </Button>     
                </Center>
            </form>
            </div>
            <FormFooter/>    
        </div>
        
    )
}

export default Form